.slider {
    display: flex;
    overflow: hidden;
    width: 300px;
    height: 200px;
  }
  
  .slider-item {
    flex: 0 0 100%;
    transition: transform 0.3s ease-in-out;
  }
  
  .slider-item.active {
    transform: translateX(-100%);
  }
  
  .spin-button {
    margin-top: 10px;
  }